import React from 'react'

function ImageHeader() {
    return (
        <div class="container">
        <div class="row">
          <div class="col align-self-start">
          <img class="img-fluid rounded" src="images/Image1.jpg" />
          </div>
          <div class="col align-self-center">
          <img class="img-fluid rounded" src="images/Image2.jpg" />
          </div>
        </div>
      </div>
    );
}

export default ImageHeader;