import React from "react";

function Card() {
  return (
    <div class="container">
      <div class="row">
        <div class="card">
          <h5 class="card-title">About Me</h5>
          <hr></hr>
          <div class="card-body">
            <p>
              Hi, I'm Tommaso Framba. I'm an incoming junior studying Computer
              Science at UCSC. <br></br>
              <br></br>
              My career interests lie in full-stack software engineering and
              Machine Learning applications. I love working with a vast array of
              different technologies. I have been working for half a year as a
              contractor in front end web dev for a solar company and have also
              created my own app as a personal side project that generated over
              thirty five thousand downloads.{" "}
            </p>
          </div>
        </div>

        <div class="card">
          <h5 class="card-title">More Specifically...</h5>
          <hr></hr>
          <div class="card-body">
            <p>
              Most of my experience both in school and proffesionally has been
              within C++, Java, and NodeJS. I have experience working with
              frontend JS libraries as well (mainly React, what this site is
              built with).
              <br></br>
              <br></br>
              Ideally, I'm looking for a role that involves Node, Python, or
              Java, but I'm always open to any opportunity that gives me the
              chance to constantly learn and be challenged.{" "}
            </p>
          </div>
        </div>
      </div>
                
     
    </div>
  );
}

export default Card;
